import React from 'react'
import PostSubline from './PostSubline'
import { Box, Heading, Text } from '@chakra-ui/react'

const ArticleExcerpt = ({
  id,
  title,
  date,
  excerpt,
  category,
  slug,
  url,
  timeToRead,
  articleLinkHandler,
  categoryLinkHandler,
}) => {
  const disqusConfig = {
    url: url,
    identifier: id,
    title: title,
  }

  return (
    <Box my="4rem">
      <Heading
        fontSize="2.5rem"
        css={`
          position: relative;
          text-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
          margin-bottom: 0.75rem;
        `}
      >
        <Text
          fontSize="7rem"
          css={`
            position: absolute;
            transform: translate(-50%, -50%);
            opacity: 0.08;
            user-select: none;
          `}
        >
          {title.charAt(0)}
        </Text>
        <a href="#" onClick={() => articleLinkHandler(slug)}>
          {title}
        </a>
      </Heading>
      <PostSubline
        date={date}
        config={disqusConfig}
        timeToRead={timeToRead}
        category={category}
        categoryLinkHandler={categoryLinkHandler}
      />
      <Box my="1rem">{excerpt}</Box>
    </Box>
  )
}

export default ArticleExcerpt
