import React from 'react'
import { Icon } from '../misc/Icon'
import { Timeline } from './Timeline'
import { Box, Grid, GridItem } from '@chakra-ui/react'

const Resume = ({ metadata }) => (
  <section data-id="resume" data-title="Resume">
    <Box
      sx={{
        '@media print': {
          display: 'none',
        },
      }}
    >
      <h1>Hi,</h1>
    </Box>
    <Grid templateColumns="repeat(3, 1fr)" columnGap={10}>
      <GridItem
        colSpan={[3, 3, 3, 2]}
        mb={['1rem', '1rem', '1rem', 0]}
        sx={{
          '@media print': {
            marginBottom: 0,
          },
        }}
      >
        <div dangerouslySetInnerHTML={{ __html: metadata.author.bio }} />
        <Timeline>
          {metadata.resume.timeline.map((type) => (
            <div key={type.name}>
              <Box mb={'1rem'}>
                <h3>{type.name}</h3>
              </Box>
              {type.events.map((event, index) => (
                <Box mb={'1rem'} key={index}>
                  <ul className="timeline">
                    <li key={event.title}>
                      {event.startDate && (
                        <span className="timeline_time">
                          {event.startDate} -{' '}
                          {event.endDate ? event.endDate : 'Present'}
                        </span>
                      )}
                      <div className="timeline_icon d-print-none">
                        <Icon name={event.type} />
                      </div>
                      <div className="timeline_content">
                        <h5
                          style={{
                            marginBottom: 2,
                          }}
                        >
                          {event.title}
                        </h5>
                        <strong>{event.subtitle}</strong>
                        {event.overview && <div>{event.overview}</div>}
                        {event.points && (
                          <ul>
                            {event.points.map((point, index) => (
                              <li key={index}>{point}</li>
                            ))}
                          </ul>
                        )}
                        {event.link && (
                          <a
                            href={event.link}
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            {event.link}
                          </a>
                        )}
                      </div>
                    </li>
                  </ul>
                </Box>
              ))}
            </div>
          ))}
        </Timeline>
      </GridItem>
      <GridItem colSpan={[3, 3, 3, 1]}>
        <h3>Skills</h3>
        <Grid templateColumns="repeat(2, 1fr)" columnGap={5}>
          {metadata.resume.skills.map((skill) => (
            <GridItem
              colSpan={[2, 1, 1, 2]}
              rowSpan={1}
              mb={'1rem'}
              key={skill.name}
              sx={{
                '@media print': {
                  marginBottom: 0,
                },
              }}
            >
              <strong>{skill.name}</strong>
              <Box
                className="progress"
                css={`
                  background-color: ${(props) =>
                    props.theme.light.colors.secondary} !important;

                  @media (prefers-color-scheme: dark) {
                    background-color: ${(props) =>
                      props.theme.dark.colors.secondary} !important;
                  }
                `}
                sx={{
                  '@media print': {
                    display: 'none',
                  },
                }}
              >
                <Box
                  className="progress-bar"
                  sx={{
                    width: `${skill.level}%`,
                  }}
                  css={`
                    background-color: ${(props) =>
                      props.theme.light.colors.primary} !important;

                    @media (prefers-color-scheme: dark) {
                      background-color: ${(props) =>
                        props.theme.dark.colors.primary} !important;
                    }
                  `}
                />
              </Box>
            </GridItem>
          ))}
        </Grid>
      </GridItem>
    </Grid>
  </section>
)

export default Resume
