import React from 'react'
import { Box, Grid, GridItem } from '@chakra-ui/react'
import { darken } from 'polished'

const PrevNext = ({
  prev,
  prevText,
  prevLinkHandler,
  next,
  nextText,
  nextLinkHandler,
}) => {
  return (
    <>
      {(prev || next) && (
        <Grid my="3rem" textAlign="center" templateColumns="repeat(2, 1fr)">
          <GridItem
            colSpan={1}
            css={`
              float: left;
              background-color: ${(props) =>
                props.theme.light.colors.secondary};
              :hover:not(.disabled) {
                background-color: ${(props) => props.theme.dark.colors.primary};
                cursor: pointer;
              }

              @media (prefers-color-scheme: dark) {
                background-color: ${(props) =>
                  darken(0.05, props.theme.dark.colors.secondary)};
                :hover:not(.disabled) {
                  background-color: ${(props) =>
                    props.theme.dark.colors.primary};
                }
              }
            `}
          >
            {prev && (
              <Box p="1em" onClick={() => prevLinkHandler(prev)}>
                {prevText}
              </Box>
            )}
          </GridItem>
          <GridItem
            colSpan={1}
            css={`
              float: right;
              background-color: ${(props) =>
                darken(0.05, props.theme.light.colors.secondary)};
              :hover:not(.disabled) {
                cursor: pointer;
                background-color: ${(props) =>
                  props.theme.light.colors.primary};
              }

              @media (prefers-color-scheme: dark) {
                a {
                  color: ${(props) => props.theme.dark.colors.primary};
                }
                background-color: ${(props) =>
                  darken(0.05, props.theme.dark.colors.secondary)};
                :hover:not(.disabled) {
                  background-color: ${(props) =>
                    props.theme.dark.colors.primary};
                }
              }
            `}
          >
            {next && (
              <Box p="1em" onClick={() => nextLinkHandler(next)}>
                {nextText}
              </Box>
            )}
          </GridItem>
        </Grid>
      )}
    </>
  )
}

export default PrevNext
